:root {
  --borderRadius : 10px;
}

body {
  margin: 100px;
}

.App {
  text-align: center;
  height: 100%;
  padding: 20px;
  background: #70c09b; /* fallback for old browsers */
  background: linear-gradient(to right, #70c09b, #83bad4);
  color: black;
}

.modal-component {
  width: auto;
  margin:0 auto;
  padding: 50px;
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  background: linear-gradient(to right, #707fc0, #d483b5);
  z-index: 4;
  border-radius: var(--borderRadius);
}

.overlay {
  position: fixed;
  inset: 0px;
  background-color: rgba(152, 231, 156, 0.75);
  z-index: 5;
}

.tail-spin-wrapper {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 5;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  color: red;
}

.loading {
  filter: blur(1px);
}

.btn {
  border: 1px solid #f3ebeb;
  border-radius: var(--borderRadius);
  outline: none;
  padding: 12px 16px;
  margin: 1px;
  background-color: white;
  cursor: pointer;
  transition: all 0.1s ease-in;
}

.btn:hover {
  background-color: #ddd;  
}

.btn:disabled {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

.btn_location_normal {
  background-color: white;
}

.btn_location_active {
  background-color: #666;
  color: white;
}
